<template>
  <v-container>
    <div>
      <v-card tile class="mx-auto">
        <v-img
          position="bottom"
          class="white--text"
          height="70px"
          src="@/assets/card-bg.jpg"
        >
          <v-card-text class="text-right text-h6 font-weight-black">
            CASEFILE LIST
          </v-card-text>
        </v-img>
      </v-card>

      <v-data-table
        dense
        :headers="headers"
        :items="casefiles"
        :search="search"
        class="elevation-1"
      >
        <template v-slot:top>
          <v-toolbar flat>
            <v-dialog v-model="dialog" max-width="500px">
              <template v-slot:activator="{ on: dialog, attrs }">
                <v-tooltip bottom>
                  <template v-slot:activator="{ on: tooltip }">
                    <v-btn
                      fab
                      small
                      color="primary"
                      dark
                      v-bind="attrs"
                      v-on="{ ...tooltip, ...dialog }"
                    >
                      <v-icon>mdi-plus</v-icon>
                    </v-btn>
                  </template>
                  <span>ADD CASEFILE</span>
                </v-tooltip>
              </template>

              <v-card>
                <v-card-title>
                  <span class="text-h5">{{ formTitle }}</span>
                </v-card-title>

                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-col cols="12" sm="6" md="6">
                        <v-select
                          v-model="editedItem.refType"
                          :items="refType"
                          item-text="description"
                          item-value="name"
                          :menu-props="{ maxHeight: '400' }"
                          label="DEPARTMENT"
                          hint="Set the casefile department"
                          persistent-hint
                        ></v-select>
                      </v-col>
                      <v-col cols="12" sm="6" md="6">
                        <v-select
                          v-model="editedItem.year"
                          :items="year"
                          label="Year"
                        ></v-select>
                      </v-col>

                      <v-col cols="12" sm="6" md="6">
                        <v-select
                          v-model="editedItem.branch"
                          :items="branch"
                          item-text="name"
                          item-value="brCode"
                          :menu-props="{ maxHeight: '400' }"
                          label="BRANCH"
                          hint="Set the casefile branch"
                          persistent-hint
                        ></v-select>
                      </v-col>
                      <v-col cols="12" sm="6" md="6">
                        <v-select
                          v-model="editedItem.insurer"
                          :items="insurer"
                          item-text="name"
                          item-value="insCode"
                          :menu-props="{ maxHeight: '400' }"
                          label="INSURER"
                          hint="Set the casefile insurer"
                          persistent-hint
                        ></v-select>
                      </v-col>
                      <v-col cols="12" sm="6" md="6">
                        <v-text-field
                          v-model="editedItem.claimNo"
                          :rules="[
                            (v) => !!v || 'Insurers reference is required',
                          ]"
                          label="Insurers Reference"
                          required
                        ></v-text-field>
                      </v-col>

                      <v-col cols="12" sm="6" md="6">
                        <v-text-field
                          v-model="editedItem.vehicleNo"
                          :rules="[(v) => !!v || 'Vehicle Number is required']"
                          label="Vehicle Number Reference"
                          required
                        ></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="6">
                        <template>
                          <div>
                            <!-- <div class="mb-6">
                            Active picker:
                            <code>{{ activePicker || "null" }}</code>
                          </div> -->
                            <!-- <v-menu
                              ref="menu"
                              v-model="menu"
                              :close-on-content-click="false"
                              transition="scale-transition"
                              offset-y
                              min-width="auto"
                            >
                              <template v-slot:activator="{ on, attrs }">
                                <v-text-field
                                  v-model="editedItem.dateOfLoss"
                                  label="Date of Loss"
                                  prepend-icon="mdi-calendar"
                                  readonly
                                  v-bind="attrs"
                                  v-on="on"
                                ></v-text-field>
                              </template>
                              <v-date-picker
                                v-model="editedItem.dateOfLoss"
                                :active-picker.sync="activePicker"
                                :max="
                                  new Date(
                                    Date.now() -
                                      new Date().getTimezoneOffset() * 60000
                                  )
                                    .toISOString()
                                    .substr(0, 10)
                                "
                                min="1950-01-01"
                                @change="save"
                              ></v-date-picker>
                            </v-menu> -->

                            <v-menu
                              v-model="menu"
                              :close-on-content-click="false"
                              max-width="290"
                            >
                              <template v-slot:activator="{ on, attrs }">
                                <v-text-field
                                  v-model="editedItem.dateOfLoss"
                                  :value="computedDateFormattedMomentjs"
                                  clearable
                                  label="Date Of Loss"
                                  prepend-icon="mdi-calendar"
                                  readonly
                                  v-bind="attrs"
                                  v-on="on"
                                  @click:clear="date = null"
                                ></v-text-field>
                              </template>
                              <v-date-picker
                                v-model="editedItem.dateOfLoss"
                                :value="computedDateFormattedMomentjs"
                                @change="menu = false"
                              ></v-date-picker>
                            </v-menu>
                          </div>
                        </template>
                      </v-col>
                      <v-col cols="12" sm="6" md="6">
                        <v-text-field
                          v-model="editedItem.placeOfLoss"
                          :rules="[(v) => !!v || 'Place of Loss is required']"
                          label="Place of Loss"
                          required
                        ></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="6">
                        <v-text-field
                          v-model="editedItem.insuredName"
                          :rules="[(v) => !!v || 'Insured Name is required']"
                          label="Insured Name"
                          required
                        ></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="6">
                        <v-text-field
                          v-model="editedItem.insComment"
                          label="Insurers Comment"
                        ></v-text-field>
                      </v-col>

                      <v-col cols="12" sm="6" md="6">
                        <v-btn
                          :disabled="isAddButtonDisabled"
                          color="primary"
                          class="mt-3"
                          @click="saveCasefile"
                          >Submit</v-btn
                        >
                      </v-col>
                    </v-row>
                  </v-container>
                </v-card-text>
              </v-card>
            </v-dialog>

            <v-divider class="mx-4" inset vertical></v-divider>
            <v-dialog v-model="dialogDelete" max-width="500px">
              <v-card>
                <v-card-title class="text-h5"
                  >Are you sure you want to delete the casefile with id
                  {{ editedItem.id }}?</v-card-title
                >
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="closeDelete"
                    >Cancel</v-btn
                  >
                  <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                    >OK</v-btn
                  >
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
            </v-dialog>
            <v-spacer></v-spacer>
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Search"
              single-line
              hide-details
              clearable
            ></v-text-field>
          </v-toolbar>
        </template>

        <template slot="item.numbering" slot-scope="props">
          {{ props.index + 1 }}
        </template>
        <template v-slot:[`item.id`]="{ item }">
          <tr>
            AA/{{
              item.refType
            }}/{{
              item.id
            }}/{{
              item.year
            }}/{{
              item.branch
            }}
          </tr>
        </template>

        <template v-slot:[`item.dateOfLoss`]="{ item }">
          {{ formatDate(item.dateOfLoss) }}
        </template>

        <template v-slot:[`item.actions`]="{ item }">
          <v-tooltip left transition="fab-transition">
            <template v-slot:activator="{ on, attrs }">
              <v-icon
                small
                v-bind="attrs"
                v-on="on"
                class="mr-2"
                @click="editItem(item)"
              >
                mdi-pencil
              </v-icon>
            </template>
            <span> edit</span>
          </v-tooltip>
          <v-tooltip right transition="fab-transition">
            <template v-slot:activator="{ on, attrs }">
              <v-icon
                small
                v-bind="attrs"
                v-on="on"
                color="red darken-1"
                @click="deleteItem(item)"
              >
                mdi-delete
              </v-icon>
            </template>
            <span> delete</span>
          </v-tooltip>
        </template>
      </v-data-table>
    </div>
  </v-container>
</template>

<script>
import BranchDataService from "../../services/BranchDataService";
import DeptDataService from "../../services/DeptDataService";
import InsDataService from "../../services/InsDataService";
import CasefileDataService from "../../services/CasefileDataService";
import moment from "moment";

export default {
  name: "casefiles",
  data: () => ({
    dialog: false,
    dialogDelete: false,
    casefiles: [],
    search: "",

    // activePicker: null,
    // date: format(parseISO(new Date().toISOString()), "yyyy-MM-dd"),
    date: null,
    menu: false,

    branch: [],
    refType: [],
    insurer: [],
    year: ["2018", "2019", "2020", "2021", "2022"],
    defaultItem: {
      id: null,
      refType: "",
      year: "",
      branch: "",
      insurer: "",
      claimNo: "",
      vehicleNo: "",
      dateOfLoss: "",
      placeOfLoss: "",
      insuredName: "",
      insComment: "",
      createdBy: "",
      fileStatus: "NEW",
    },
    editedIndex: -1,

    editedItem: {
      id: null,
      refType: "",
      year: "",
      branch: "",
      insurer: "",
      claimNo: "",
      vehicleNo: "",
      dateOfLoss: "",
      placeOfLoss: "",
      insuredName: "",
      insComment: "",
      createdBy: "",
      fileStatus: "NEW",
    },

    headers: [
      {
        text: "NO.",
        align: "start",
        sortable: false,
        value: "numbering",
      },
      {
        text: "INSURER",
        align: "center",
        value: "insurer",
        sortable: false,
      },
      {
        text: "VEHICLE NO.",
        align: "center",
        value: "vehicleNo",
        sortable: false,
      },
      {
        text: "AASB REFERENCE",
        align: "center",
        value: "id",
        sortable: false,
      },
      {
        text: "CLAIM NO.",
        align: "start",
        sortable: false,
        value: "claimNo",
      },
      {
        text: "DATE OF LOSS",
        align: "start",
        sortable: false,
        value: "dateOfLoss",
      },

      {
        text: "STATUS",
        align: "center",
        value: "fileStatus",
        sortable: false,
      },

      {
        text: "ACTIONS",
        align: "center",
        value: "actions",
        sortable: false,
      },
    ],
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },
    isAddButtonDisabled() {
      return !(
        this.editedItem.refType &&
        this.editedItem.year &&
        this.editedItem.branch &&
        this.editedItem.insurer &&
        this.editedItem.claimNo &&
        this.editedItem.vehicleNo &&
        this.editedItem.dateOfLoss &&
        this.editedItem.placeOfLoss &&
        this.editedItem.insuredName &&
        this.editedItem.insComment &&
        // this.editedItem.createdBy &&
        this.editedItem.fileStatus
      );
    },
    computedDateFormattedMomentjs() {
      return this.date ? moment(this.date).format("yyyy-MM-dd") : "";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  methods: {
    formatDate(value) {
      return moment(value).format("DD/MMM/YY");
    },

    retrieveCasefiles() {
      CasefileDataService.getAll()
        .then((response) => {
          // this.casefiles = response.data.map(this.getDisplayCasefile);
          this.casefiles = response.data;
          // console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },

    // getDisplayCasefile(casefile) {
    //   return {
    //     id: casefile.id,
    //     name:
    //       casefile.name.length > 30
    //         ? casefile.name.substr(0, 30) + "..."
    //         : casefile.name,
    //     id:
    //       casefile.id.length > 30
    //         ? casefile.id.substr(0, 30) + "..."
    //         : casefile.id,
    //   };
    // },

    refreshList() {
      this.retrieveCasefiles();
    },

    retrieveDept() {
      DeptDataService.getAll()
        .then((response) => {
          this.refType = response.data;
        })
        .catch((e) => {
          console.log(e);
        });
    },

    retrieveIns() {
      InsDataService.getAll()
        .then((response) => {
          this.insurer = response.data;
        })
        .catch((e) => {
          console.log(e);
        });
    },

    save(date) {
      this.$refs.menu.save(date);
    },

    retrieveBranches() {
      BranchDataService.getAll()
        .then((response) => {
          this.branch = response.data.map(this.getDisplayBranch);
        })
        .catch((e) => {
          console.log(e);
        });
    },

    getDisplayBranch(branch) {
      return {
        id: branch.id,
        name:
          branch.name.length > 30
            ? branch.name.substr(0, 30) + "..."
            : branch.name,
        brCode:
          branch.brCode.length > 30
            ? branch.brCode.substr(0, 30) + "..."
            : branch.brCode,
      };
    },

    editItem(item) {
      this.editedIndex = this.casefiles.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.casefiles.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      CasefileDataService.delete(this.editedItem.id)
        .then((response) => {
          this.$store.dispatch("setSnackbar", {
            showing: true,
            color: "error",
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });

          this.refreshList();
        })
        .catch((e) => {
          console.log(e);
        });

      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    saveCasefile() {
      if (this.editedIndex > -1) {
        CasefileDataService.update(this.editedItem.id, this.editedItem)
          .then((response) => {
            this.close();
            this.$store.dispatch("setSnackbar", {
              showing: true,
              color: "warning",
              text:
                (response && response.data && response.data.message) ||
                toString(),
            });
            this.refreshList();
          })
          .catch((e) => {
            console.log(e);
          });
      } else {
        var data = {
          refType: this.editedItem.refType,
          year: this.editedItem.year,
          branch: this.editedItem.branch,
          insurer: this.editedItem.insurer,
          claimNo: this.editedItem.claimNo,
          vehicleNo: this.editedItem.vehicleNo,
          dateOfLoss: this.editedItem.dateOfLoss,
          placeOfLoss: this.editedItem.placeOfLoss,
          insuredName: this.editedItem.insuredName,
          insComment: this.editedItem.insComment,
          createdBy: this.$store.state.auth.user.username,
          fileStatus: this.editedItem.fileStatus,
        };

        CasefileDataService.create(data)
          .then((response) => {
            this.editedItem.id = response.data.id;
            this.close();
            this.$store.dispatch("setSnackbar", {
              showing: true,
              color: "success",
              text:
                (response && response.data && response.data.message) ||
                toString(),
            });
            this.refreshList();
          })
          .catch((e) => {
            this.$store.dispatch("setSnackbar", {
              showing: true,
              text:
                (e.response && e.response.data && e.response.data.message) ||
                e.message ||
                toString(),
            });
          });
      }
      this.close();
    },
  },
  mounted() {
    this.retrieveBranches();
    this.retrieveDept();
    this.retrieveIns();
    this.retrieveCasefiles();
  },
};
</script>
